<template>
  <div class="main">
    <input
      type="text"
      class="title"
      v-model="usertitle"
      placeholder="标题"
      ref="txt"
    />
    <textarea
      class="text"
      v-model="userinput"
      placeholder="内容"
      @keyup.enter="senduserinput"
    ></textarea>
    <div class="btns">
      <span><button @click="senduserinput">发布</button></span>
      <samp><button>编辑</button></samp>
    </div>
  </div>
</template>

<script>
import { suijishu } from "../mixin/randomNumber";
export default {
  name: "edit",
  mixins: [suijishu],
  data() {
    return {
      usertitle: "",
      userinput: "",
    };
  },
  mounted() {
    this.$refs.txt.focus();
  },
  methods: {
    senduserinput() {
      function getDatetime() {
        //获取当前的年月日
        let dates = new Date();
        let year = dates.getFullYear();
        let month = dates.getMonth() + 1;
        let day = dates.getDate();

        return {
          year,
          month,
          day,
          sendtime: `${year}-${month}-${day}`,
        };
      }
      const userdataOBJ = {
        id: suijishu.randomNumber(),
        text: this.userinput,
        title: this.usertitle,
        SendTime: getDatetime(),
      };
      if (this.usertitle != "" && this.userinput != "") {
        this.$store.commit("SENDUSERINPUT", userdataOBJ);
        this.userinput = null;
        this.$router.push({
          name: "home",
        });
      } else {
        alert("标题和内容不能为空");
      }
    },
  },
};
</script>

<style scoped>
.main {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #eea2a4;
}
.main .title {
  width: 50vw;
  height: 3rem;
  border: 0;
  outline: none;
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
  box-sizing: border-box;
  padding: 9px 18px;
  font: 22px "";
  border-bottom: 1px solid rgba(255, 255, 255, 0.555);
  background-color: rgba(255, 255, 255, 0.714);
}
.main .text {
  outline: none;
  resize: none;
  width: 50vw;
  height: 25vh;
  box-sizing: border-box;
  padding: 9px 18px;
  font: 23px "";
  background-color: rgba(255, 255, 255, 0.714);
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
  border: 0;
}
.btns {
  margin-top: 14px;
}
.btns button {
  width: 20vw;
  height: 2.5rem;
  font: 700 16px "";
  border: 0;
  border-radius: 6px;
  margin: 0 5px;
  background-color: #93b5cfcb;
}
</style>
